import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from '../../../components/common/InfoCard/index';
import {
  getQuickLinkBonusGetBonusTypeChildNo,
  getQuickLinkBonusGetBonusTypeNo,
  getQuickLinkBonusGetBonusNoteNo,
  getQuickLinkBonusGetPrizeClassificationNo,
  getQuickLinkBonusGetPrizeNoteNo,
} from '../../../services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Cấp khen thưởng',
    service: getQuickLinkBonusGetBonusTypeNo,
    url: '/bonus-type/list',
  },
  {
    title: 'Loại khen thưởng',
    service: getQuickLinkBonusGetBonusTypeChildNo,
    url: '/typeOfRewards/list',
  },
  {
    title: 'QĐ khen thưởng',
    service: getQuickLinkBonusGetBonusNoteNo,
    url: '/reward/add',
  },
  {
    title: 'Xếp loại học bổng',
    service: getQuickLinkBonusGetPrizeClassificationNo,
    url: '/scholarshipRatings/list',
  },
  {
    title: 'QĐ học bổng',
    service: getQuickLinkBonusGetPrizeNoteNo,
    url: '/scholarship/add',
  },
];

class DashboardBonu extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardBonu {...props} />
  </Page>
);



